import * as React from 'react';
import { IHeadingButtonProps } from "./IHeadingButtonProps";
export class HeadingButton extends React.Component<IHeadingButtonProps> {
    constructor(props: IHeadingButtonProps) {
        super(props);
    }

    render() {
        return (
            <a href={this.props.href}>
                <i className={this.props.icon}></i>
            </a>
        );
    }
}
